<template>
    <div class="queryuser">

        <!-- 1.条件筛选 -->
        <el-row>
            <el-col :span="22">
                <el-collapse v-model="activeNames" @change="handleChange">
                    <el-collapse-item title="条件筛选" name="1">
                        <!-- 表单开始 -->
                        <el-form :model="searchForm" ref="searchForm" label-width="85px">
                            <el-form-item label="商店名称：" prop="shopname" >
                                <el-input v-model="searchForm.shopname" placeholder="根据商店名称进行查询"></el-input>
                            </el-form-item>
                            <el-form-item label="电话号码：" prop="telnum">
                                <el-input v-model="searchForm.telnum" placeholder="根据电话号码进行查询"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button size="small" icon="el-icon-s-help" @click="submitForm('searchForm')">条件查询</el-button>
                                <el-button @click="resetForm('searchForm')">重置</el-button>
                            </el-form-item>
                        </el-form>
                        <!-- 表单结束 -->
                    </el-collapse-item>
                </el-collapse>
            </el-col>
        </el-row>


        <!-- 2.搜索框 -->
        <el-row>
            <el-col :span="2">
                <el-button size="small" icon="el-icon-delete" class="btn-deletes">批量删除</el-button>
            </el-col>
            <el-col :span="2">
                <el-button size="small" icon="el-icon-refresh" class="btn-refresh" @click="QueryUser();">刷新</el-button>
            </el-col>
            <el-col :span="10" style="min-height: 2em;"></el-col>
            <el-col :span="8">
                <el-input placeholder="请输入用户ID编号" v-model="serchuid" class="input-with-select" clearable>
                    <el-button slot="append" icon="el-icon-search" @click="QueryUserById();">查询</el-button>
                </el-input>
            </el-col>
        </el-row>


        <!-- 3.表格 -->
        <el-row>
            <el-col :span="22">
                <el-table
                    border
                    :data="dataList"
                    style="width: 100%">
                    <el-table-column
                    type="selection"
                    width="55">
                    </el-table-column>
                    <el-table-column
                    label="ID编号"
                    width="170">
                        <template slot-scope="scope">
                            <span>{{ scope.row.userId }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                    label="姓名"
                    width="100">
                        <template slot-scope="scope">
                            <span>{{ scope.row.userUserName }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="电话">
                        <template slot-scope="scope">
                            <span>{{ scope.row.userTelphone }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="地址" width="100%">
                        <template slot-scope="scope">
                            <span>{{ scope.row.userAddress }}</span>
                        </template>
                    </el-table-column>
                     <el-table-column label="商店名称">
                        <template slot-scope="scope">
                            <span>{{ scope.row.userShopName }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="状态">
                        <template slot-scope="scope">
                            <span>{{ scope.row.userStatus }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" class="el-icon-edit" @click="openEditDialog(scope.$index);">编辑</el-button>
                            <el-button type="text" size="small" class="el-icon-delete" @click="deleteOP(scope.$index);">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>


        <!-- 弹框开始 -->
        <el-dialog
        title="编辑用户"
        :visible.sync="dialogVisible"
        :before-close="handleClose">
            <div>
                <i-showuser ref="ChildDialog"></i-showuser>
            </div>
            <span slot="footer" class="dialog-footer">
                <!-- 提交修改axios -->
                <el-button type="primary" @click="confirmModify('ChildDialog');dialogVisible = false;">确认修改</el-button>
                <el-button @click="dialogVisible = false">取 消</el-button>
            </span>
        </el-dialog>
        <!-- 弹框结束 -->

        <!-- 分页 -->
        <el-row>
            <el-col :span="24">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                </el-pagination>
            </el-col>
        </el-row>

    </div>
</template>

<script>
import ShowUser from './ShowUser.vue'
export default {
    components : {
        "i-showuser" : ShowUser
    },
    data(){
        return {
            activeNames : ['0'],    // 折叠面板
            dialogVisible: false,   // 对话框显示
            serchuid : '',          // 根据ID查询绑定   
            searchForm : {          // 多条件查询绑定
                shopname : '',
                telnum : '',
            },
            // 表格数据源
            dataList: [{
                userId : '',
                userUserName : '',
                userPassword : '',
                userTelphone : '',
                userAddress : '',
                userShopName : '',
                userComment : '',
                userStatus : ''
            }],
            currentEditIndex : 0,
            currentPage : 1,
            pageSize : 8,
            total : 100
        }
    },
    created(){
        this.inite();
    },
    methods : {
        inite(){
            // 表格初始状态不显示内容
            this.dataList = [];
        },

        // 多条件查询用户
        QueryUser(){
            this.$axios.get("http://www.wxhwg.com:8080/mock/user/list",{
                params : {
                    shopName : this.searchForm.shopname,
                    telphone : this.searchForm.telnum,
                    pageNum	 : this.currentPage,
                    pageSize : this.pageSize
                }
            }).then(response=>{
                let result = response.data;
                if(result.status == "200"){
                    this.dataList = result.data.list;
                    this.total = result.data.total;
                    // this.$message.success("查询成功！");
                }else{
                    this.$message.warning("查询失败！");
                }
            }).catch(error=>{
                 this.$message.error(error);
            });
        },


        // 通过ID查询用户
        QueryUserById(){
            this.$axios.get("http://wxhwg.com:8080/mock/user/findById",{
                params : {
                    uid : this.serchuid
                }
            }).then(response=>{
                let result = response.data;
                if(result.status == "200"){
                    let list = [];
                    if(result.data != null){
                        list.push(result.data);
                    }
                    this.dataList = list;
                }else{
                    alert("查询失败！");
                }
            }).catch(error=>{
                alert(error);
            });
        },


        // 提交表单
        submitForm(formName) {
             this.QueryUser();
        },
        // 重置表单
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        handleClose(done) {
            this.$confirm('确认关闭？')
            .then(() => {
                done();
            })
            .catch(() => {});
        },
        handleChange(val) {
            console.log(val);
        },


        // 删除操作
        deleteOP(index) {
            let userid = this.dataList[index].userId;
            this.$confirm('此操作将删除用户'+userid+', 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$axios.get("http://www.wxhwg.com:8080/mock/user/del",{
                    params:{
                        uid : userid
                    }
                }).then(response=>{
                    let result = response.data;
                    if(result.status == "200"){
                        // 删除list内数据,动态更新table
                        this.dataList.splice(index,1);
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }else{
                        alert("删除错误!");
                    }
                }).catch(error=>{
                    alert(error);
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        },



        // 多选
        multipleSelection: [],
        toggleSelection(rows) {
        if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
            this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            this.$message(val);
        },


        // 打开编辑框
        openEditDialog(index){
            this.currentEditIndex = index;
            this.dialogVisible = true;
            console.log("对话框打开，修改用户："+this.dataList[index].userId);

            // 没有形成进程阻塞，不会造成性能和负载问题。使用Promise处理异步
            function sleep(ms) {
                return new Promise(resolve =>{
                     setTimeout(resolve, ms);
                });
            }
            sleep(0).then(()=>{
                // code
                if(this.$refs.ChildDialog == null){ return ;}else{console.log(this.$refs);}
                // 原因是DOM之前调用了null,第一次这里为空
                this.$refs.ChildDialog.refreshFormInfo(this.dataList[index]);
            })
            // 放在这里会造成第一次不加载$refs的现象，后续却可以加载。
            // 猜测是加载dom需要时间
            // setTimeout(() => {},0);此方法会阻塞

        },



        // 确认修改用户信息
        confirmModify(formName){
            // 获得数据
            let formData = this.$refs[formName].getFormData();
            let index = this.currentEditIndex;
            // axios请求
            let params = this.$qs.stringify({
                userId : formData.userId,
                userUserName : formData.userUserName,
                userPassword : formData.userPassword,
                userTelphone : formData.userTelphone,	
                userAddress	: formData.userAddress,
                userShopName : formData.userShopName,
                userComment : formData.userComment
            });
            this.$axios.post("http://www.wxhwg.com:8080/mock/user/update",params)
            .then(response=>{
                let result = response.data;
                if(result.status == "200"){
                    // 更新本地数据
                    this.dataList.splice(index,1);
                    this.dataList.splice(index,0,formData);
                    this.$message({
                        message: '修改用户信息成功！',
                        type: 'success'
                    });
                }else{
                    this.$message({
                        message: '修改用户信息失败！',
                        type: 'warning'
                    });
                }
            }).catch(error=>{
                this.$message.error(error);
                console.log(error);
            });
        },


        // 分页功能
        handleSizeChange(val){
            // 每页val条
            this.pageSize = val;
            this.QueryUser();
        },
        handleCurrentChange(val){
            // 当前第val页
            this.currentPage = val;
            this.QueryUser();
        }


    }
}
</script>


<style>
    .queryuser .el-dialog{
        width: 40%;
    }

    .el-select .el-input {
        width: 110px;
    }
    .input-with-select .el-input-group__prepend {
        background-color: #fff;
    }

    /* 三个按钮 */
    .btn-queryall,.btn-queryall:hover,.btn-queryall:focus{
        color: #409EFF;background: #ecf5ff;border-color: #b3d8ff;
    }
    .btn-queryall:active{
        color: #3a8ee6;background: #ecf5ff;border-color: #3a8ee6;
    }

    .btn-deletes,.btn-deletes:focus{
        color: #F56C6C;background: #fef0f0;border-color: #fbc4c4;
    }
    .btn-deletes:hover{
        color: #f56c6c;background: #FEEAEA;border-color: #fbc4c4;
    }
    .btn-deletes:active{
        color: #f56c6c;background: #fef0f0;border-color: #f56c6c;
    }

    .btn-refresh,.btn-refresh:focus{
        color: #67C23A;background: #f0f9eb;border-color: #c2e7b0;
    }
    .btn-refresh:hover{
        color: #67C23A;background: #EBFAE2;border-color: #c2e7b0;
    }
    .btn-refresh:active{
        color: #67C23A;background: #f0f9eb;border-color: #67C23A;
    }

    /* 表格 */
    .el-table thead{
        background-color:tomato;
    }
    .el-table thead th{
        background-color: #f5f5f5;
        color: #666;
        font-weight:500;
    }
    .el-table__body{
        color: #666;
    }

    /* 搜索框 */
    .queryuser .el-input-group__append:hover{
        color: #409EFF;background: #ecf5ff;
    }
    .queryuser .el-input-group__append,.queryuser .el-input-group__append:active{
        color: #3a8ee6;background: #e4f0fd;
    }

    /* 折叠面板 */
    .el-collapse-item__header{
        background-color: #f7f7f6;
        height: 42px;
        padding: 0 10px;
    }
    .el-collapse-item__wrap{
        padding: 10px 10px 0 10px;
    }
    .queryuser .el-collapse-item__wrap .el-input{
        width: 60%;
    }

    /* el-row的属性 */
    .queryuser .el-row{
        margin: 0 0 6px 0;
    }

    

</style>


